import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { TreeNode } from 'primeng/primeng';
import { WorkflowHttpService } from '../../../../workflow/http/workflow.http.service';
import { IntoFlowVBean } from './bean/into-flow-V.bean';
import { Message } from 'primeng/primeng';

@Component({
  selector: 'app-into-flow',
  templateUrl: './into-flow.component.html'
})
export class IntoFlowComponent implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  expandRecursive(node: TreeNode, isExpand: boolean) {
    node.expanded = isExpand;
    if (node.children) {
      node.children.forEach(childNode => {
        this.expandRecursive(childNode, isExpand);
      });
    }
  }
  //流程树
  fileTree: any;
  left_tree: any;
  // files: TreeNode[];
  selectedFile: TreeNode;
  intoFlowVBean: IntoFlowVBean = new IntoFlowVBean();
  //提示信息
  msgs: Message[] = [];
  // 表体内容
  tableData: any;
  fileUrl: string;//返回的路径
  selectId: string;//导出接收选中的id
  selectedList:any;//导出时勾选的集合
  constructor(
    public workflowHttpService: WorkflowHttpService,

  ) { }

  ngOnInit() {
    this.queryClick();//查询树
  }

  //点击目录查询
  nodeSelect(event) {
    this.selectedFile = event.node;
    this.selectId = event.node.id
    // 流程定义查询
    // let params = {
    //   "uniqueReqData": event.node.id,
    //   rows: this.intoFlowVBean.rows,
    //   page: this.intoFlowVBean.page
    // }
    // this.workflowHttpService.IProcessDefinitionServiceSearch(params).subscribe((data) => {
    //   if (data.msg.returnCode.code == 'success') {
    //     if (data.msg.listArray) {
    //       this.tableData = [];
    //       data.msg.listArray.forEach((v1) => {
    //         this.tableData.push(v1)
    //       });
    //       this.msgs = [];
    //       this.msgs.push({ severity: 'success', summary: '提示', detail: data.msg.returnCode.code })
    //     }

    //   } else {
    //     this.msgs = [];
    //     this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.code })
    //   }
    // },
    //   error => {
    //     this.msgs = [];
    //     this.msgs.push({ severity: 'error', summary: '错误', detail: '调用服务失败' })
    //   })
  }

  nodeUnselect(event) {
    this.msgs = [];
    this.msgs.push({ severity: 'info', summary: 'Node Unselected', detail: event.node.label });
  }
  toTree(data) {
    data.forEach(item => {
      item['label'] = item['text']
      item['value'] = item
      if (item.children) {
        this.toTree(item.children)
      }
    });
    return data
  }
  //流程目录查询
  queryClick() {
    let param = {
      uniqueReqData: '-1'
    }
    this.workflowHttpService.IProcessDirectoryServiceSearchForDesigner(param).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.left_tree = this.toTree(data.msg.listArray);
        this.left_tree.forEach(node => {
          this.expandRecursive(node, true);
        });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  // //勾选
  // onChange(node) {
  //   this.intoFlowVBean.uniqueReqData = node.id;
  //   let arrId = [];
  //   console.log(this.selectedList,222)
  // }
   //导出
  outputNum() {
    let param = { 
      uniqueReqData:this.selectedList
    }
    this.workflowHttpService.IProcessDefinitionServiceExportprocess(param).subscribe(data => {
      if (data.msg.returnCode.code=='success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '导出成功' })
        this.outValue.emit(false);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
}
