import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { WorkflowHttpService } from '../../../../workflow/http/workflow.http.service';
import { SharedBasisModule } from 'app/sharedBasis.module';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { OtherVersionBean } from './bean/other-version.bean';
// import { Running } from '../../../constant/codeValue';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
    selector: 'other-version',
    templateUrl: './other-version.html',
    styleUrls: ['./other-version.scss'],
    providers: [WorkflowHttpService, ConfirmationService, Commfunc]
})

export class OtherVersion {

    @Input() inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    precredit: string = '查看其他版本';

    // 表体内容
    tableData: any;
    otherVersionBean: OtherVersionBean = new OtherVersionBean()

    //总页数
    total: string;
    //每页多少条
    rows: number = 10;

    //模态框的标题
    headerTitle: string = "机构详情";
    //模态框是否显示
    display: boolean = false;
    showModel: string;

    //提示信息
    msgs: Message[] = [];
    //接收父级参数
    otherV: any;
    //转化的中间量自定义
    queryParm: any;
    //是否在使用
    running: any = [];
    //查看其他版本
    versionBackEditValue: any;
    //恢复到编辑版本
    versionMapValue: any;
    tabMesg: any = tableMessage;
    fileUrl;//查看其他版本
    code: any;
    constructor(
        public workflowHttpService: WorkflowHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc
    ) {
        this.codeValues() //调用方法，获取全部码值
        this.running = this.code['Running'];
    }

    ngOnInit() {
        this.otherVersionBean.rows = 10;
        this.otherVersionBean.page = 1;
        this.queryClick();
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    ngOnChanges() {
        this.fuzhi();
    }

    fuzhi() {
        this.otherV = this.inValue;
    }

    //查询
    queryClick() {
        this.otherVersionBean.GID = this.inValue.id
        this.workflowHttpService.IProcessDefinitionServicegetOtherVersionProcess(this.otherVersionBean).subscribe(data => {
            if (data.msg.returnCode.code == 'success') {
                this.tableData = data.msg.data;
                this.total = data.msg.total;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.msg.returnCode.code });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    //分页
    paginate(event) {
        //每页显示的条数
        this.otherVersionBean.rows = event.rows * 1;
        //当前页
        let currentPage = event.page + 1;
        this.otherVersionBean.page = currentPage;
        //调用查询的方法
        this.queryClick();
        this.otherVersionBean.page = 1;
    }
    //查看么流程图
    lookVersionMap(car) {
        window.open('http://' + car.fileUrl + ':8080' + '/wfd/p/editor?id=' + car.code + '&wfversion=' + car.processVersion + '&name=' + car.name)
    }
    //恢复到编辑状态
    VersionBack(car) {
        this.otherVersionBean.uniqueReqData = car.id;
        this.workflowHttpService.IProcessDefinitionServicetoEditVersion(this.otherVersionBean).subscribe(data => {
            if (data.msg.returnCode.code == 'success') {
                // this.remaind();
                this.outValue.emit(false);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.code });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    //弹框提示
    remaind() {

        // this.outValue.emit(false);
    }
}