import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { TZB_HTTP_URL } from 'app/@uisftech/common/service/http.constant';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { Message ,FileUploadModule} from 'primeng/primeng';
import { WorkflowHttpService } from '../../../../workflow/http/workflow.http.service';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
// Commfunc



@Component({
  selector: 'app-out-flow',
  templateUrl: './out-flow.component.html'
})
export class OutFlowComponent implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  DOWNLOAD: string = TZB_CUS_DOWNLOAD;
  UPLOAD: string = TZB_CUS_UPLOAD;
  //提示信息
  msgs: Message[] = [];
  uploadedFiles: any = []
  files: any = [];
  fileList: any = [];//上传文件列表
  fileUrl: string;
  fileName: string;
  //参数项
  detailValue: string;
  constructor(
    public workflowHttpService:WorkflowHttpService,
    public commfunc: Commfunc
  ) { }

  ngOnInit() {
  }
  load(event) {
    //上传成功 后台返回的 文件路径 和文件名
    if (event.xhr.response) {
      this.fileList = JSON.parse(event.xhr.response);
      this.detailValue = this.fileList;
      console.log(this.detailValue,this.fileList[0].fileName)
    }
    let param = {
      name:'wfData',
      fileName: this.fileList[0].fileName,
      // "Content-Disposition": "form-data; name=\"wfData\"; filename=\"process_KHJSKHLC&_Export.dat\"",
      // "Content-Type": "application/octet-stream"
     }
    //  let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    // commonHeader['comSERVICE_CODE'] ='IProcessDefinitionRoleService' ;
    // commonHeader['comMESSAGE_CODE'] ='exportprocess' ;
    this.workflowHttpService.IProcessDefinitionServiceImportprocess1(param).subscribe(data => {
      if (data.msg.returnCode.code=='success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '导入成功' })
        this.outValue.emit(false);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  
}
