import { Component, OnInit, Inject, forwardRef } from '@angular/core';
import { TreeNode } from 'primeng/primeng';
import { element } from "protractor";
import { WorkflowHttpService } from '../../../workflow/http/workflow.http.service';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { FlowCatalogBean } from './bean/flow-catalog.bean'
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
  selector: 'flow-catalog',
  templateUrl: './flow-catalog.component.html',
  styleUrls: ['./flow-catalog.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class FlowCatalogComponent implements OnInit {
  // 表头
  precredit: string = '流程定义';
  files: TreeNode[];
  selectedFiles: TreeNode[];
  treeList2: string;
  treeSelect2: any;
  //提示信息
  msgs: Message[] = [];
  //流程树
  fileTree: any;
  left_tree: any;
  // files: TreeNode[];
  selectedFile: TreeNode;
  // 表体内容
  tableData: any;
  flowCatalogBean: FlowCatalogBean = new FlowCatalogBean()
  //模态框的标题
  headerTitle: string = "修改";
  showModel: any;
  //模态框是否显示
  display: boolean = false;
  // 新增带入参数
  updateAddFileValue: any = [];
  //新增带入参数
  updateAddValue: any = [];
  //修改参数
  updateFileValue: any = [];
  //删除参数
  updateDeleteValue: any = [];
  // 右边参数
  //修改
  updateValue: any = [];
  updateTable: any = [];
  //test
  testSeveice: any = [];
  //传入的值
  inValue: string;
  filesTree2: any;
  text: any;
  rows: number = 10;//每页调数
  page: number = 1;//当前页
  //流程变量传给子
  queryMaintainValue: any;
  //查看其他版本传给子的值
  visionValue: any;
  tabMesg: any = tableMessage;
  itemB: any;//绑个值
  //定义一个默认初始值是-1，刚开始的时候不显示
  act: any = -1;
  fileUrl: string;//返回的路径
  uniqueReqData: string;//新增回调查询需要
  handleO: string;//操作列表显示


  waitSelUserList:any;
  invalue:any;
  aaaa:any;
  expandRecursive(node: TreeNode, isExpand: boolean) {
    node.expanded = isExpand;
    if (node.children) {
      node.children.forEach(childNode => {
        this.expandRecursive(childNode, isExpand);
      });
    }
  }
  constructor(
    public workflowHttpService: WorkflowHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) { }

  ngOnInit() {
    this.flowCatalogBean.rows = 10;
    this.flowCatalogBean.page = 1
    this.queryClick()

  }
  //异步展开
  expandAll() {

  }
  //点击目录查询
  nodeSelect(event) {
    this.selectedFile = event.node;
    this.updateDeleteValue = event.node
    // 流程定义查询
    let params = {
      "uniqueReqData": event.node.id,
      rows: this.flowCatalogBean.rows,
      page: this.flowCatalogBean.page
    }
    this.fileUrl = '';
    this.workflowHttpService.IProcessDefinitionServiceSearch(params).subscribe((data) => {
      if (data.msg.returnCode.code == 'success') {
        if (data.msg.listArray) {
          this.tableData = [];
          data.msg.listArray.forEach((v1) => {
            this.tableData.push(v1)
          });
          if (this.tableData) {
            this.tableData.forEach(item => {
              this.fileUrl = item.fileUrl;
            })
          }
          this.msgs = [];
          this.msgs.push({ severity: 'success', summary: '提示', detail: data.msg.returnCode.code })
        }

      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.code })
      }
    },
      error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '错误', detail: '调用服务失败' })
      })
  }

  nodeUnselect(event) {
    this.msgs = [];
    this.msgs.push({ severity: 'info', summary: 'Node Unselected', detail: event.node.label });
  }
  toTree(data) {
    data.forEach(item => {
      item['label'] = item['text']
      item['value'] = item
      if (item.children) {
        this.toTree(item.children)
      }
    });
    return data
  }
  //流程目录查询
  queryClick() {
    let param = {
      uniqueReqData: '-1'
    }
    this.workflowHttpService.IProcessDirectoryServiceSearch(param).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.left_tree = this.toTree(data.msg.listArray);
        this.left_tree.forEach(node => {
          this.expandRecursive(node, true);
        });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }


  //新增修改后的查询服务
  query() {
    this.workflowHttpService.IProcessDefinitionServiceSearch({ "uniqueReqData": this.updateDeleteValue.id }).subscribe((data) => {
      if (data.msg.returnCode.code == 'success') {
        if (data.msg.listArray) {
          this.tableData = [];
          data.msg.listArray.forEach((v1) => {
            this.tableData.push(v1)
          });
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.code })
      }
    },
      error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '错误', detail: '调用服务失败' })
      })
  }
  // 文件夹的增删改查
  //  增加文件夹
  addFile() {
    this.headerTitle = '流程目录增加';
    this.display = true;
    this.updateAddFileValue = this.selectedFile;
    this.showModel = 1;
  }
  //添加后返回的值
  addFileCall(param) {
    this.display = param;
    this.queryClick();
  }

  // 修改文件夹
  updateFile(item) {
    this.headerTitle = '流程目录修改';
    this.display = true;
    this.updateFileValue = this.selectedFile;
    this.showModel = 2;
  }
  //修改的回调
  updateFileCall(param) {
    this.display = param;
    let paramId = {
      uniqueReqData: '-1'
    }
    this.workflowHttpService.IProcessDirectoryServiceSearch(paramId).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.left_tree = this.toTree(data.msg.listArray);
        this.left_tree.forEach(node => {
          this.expandRecursive(node, true);
          this.msgs = [];
          this.msgs.push({ severity: 'success', summary: '提示', detail: '修改成功' })
        });
      }
    })
  }



  // 删除文件夹
  deleteFile(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = { "uniqueReqData": this.updateDeleteValue.id };
        this.workflowHttpService.IProcessDirectoryServiceDelete(param).subscribe(data => {
          if (data.msg.returnCode.code == 'success') {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: '删除成功' });
            this.queryClick();
            this.selectedFile={};
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });//特殊提示信息特殊处理
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }

  // 导入
  into() {
    this.headerTitle = '导入';
    this.showModel = 11;
    this.display = true;
  }
  intoCall(param) {
    this.display = param;
    this.msgs = [];
    this.msgs.push({ severity: 'success', summary: '提示', detail: '导入成功' })
  }
  // 导出
  out() {
    this.headerTitle = '导出';
    this.showModel = 10;
    this.display = true;
  }
  outCall(param) {
    this.display = param;
    this.msgs = [];
    this.msgs.push({ severity: 'success', summary: '提示', detail: '导出成功，请到浏览器下载目录查看导出文件' })
  }
  /**
   * 流程内容
   */
  // 新增
  add() {
    this.headerTitle = '新增流程';
    this.display = true;
    this.updateAddValue = this.selectedFile;
    this.showModel = 4;
  }

  //新增后返回的值
  addCall(param) {
    this.display = param;
    this.query();
  }

  // 修改
  update(item) {
    this.headerTitle = '流程修改';
    // this.updateValue = this.selectedFile;
    this.updateTable = item;
    this.updateValue = item;
    this.display = true;
    this.showModel = 5;
  }

  //修改的回调
  updateCall(param) {
    this.display = param;
    this.flowCatalogBean = new FlowCatalogBean();
    this.workflowHttpService.IProcessDefinitionServiceSearch({ "uniqueReqData": this.updateDeleteValue.id }).subscribe((data) => {
      if (data.msg.returnCode.code == 'success') {
        if (data.msg.listArray) {
          this.tableData = [];
          data.msg.listArray.forEach((v1) => {
            this.tableData.push(v1)
          });
          this.msgs = [];
          this.msgs.push({ severity: 'success', summary: '提示', detail: '流程目录修改成功' })
        }

      }
    }
    )
  }


  //删除流程
  delete(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = { "uniqueReqData": item.id };
        this.workflowHttpService.IProcessDefinitionServiceDeleteProcess(param).subscribe(data => {
          if (data.msg.returnCode) {
            if (data.msg.returnCode.code == 'success') {
              this.msgs = [];
              this.msgs.push({ severity: 'success', summary: '提示', detail: '删除成功' });
              this.query();
            } else {
              this.msgs = [];
              this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });
            }
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
        })
      }
    });
  }

  // 设计
  design(item) {
    window.open('http://' + item.fileUrl + ':8080' + '/wfd/p/editor?id=' + item.code + '&wfversion=' + item.processVersion + '&name=' + item.name)
  }

  //  发布
  release(item) {
    this.workflowHttpService.IWorkFlowServicedeployProcess({ "uniqueReqData": item.id }).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '发布成功' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
    })
  }

  // 变量维护
  maintain(item) {
    this.headerTitle = '流程变量信息';
    this.display = true;
    this.showModel = 6;
    this.queryMaintainValue = item;
  }
  queryMaintainCall(param) {
    this.display = param;
    this.queryClick();
  }

  // 访问控制
  visit() {
    this.headerTitle = '流程权限列表';
    this.display = true;
    this.showModel = 7;
  }

  // 使用情况
  use() {
    this.headerTitle = '流程使用情况';
    this.display = true;
    this.showModel = 8;
  }

  // 查看其他版本
  lookVersion(item) {
    this.headerTitle = '流程版本信息';
    this.display = true;
    this.showModel = 9;
    this.visionValue = item;
  }
  //查看其他版本回调
  otherVersion(param) {
    this.display = param;
    this.queryClick();
    this.msgs = [];
    this.msgs.push({ severity: 'success', summary: '提示', detail: '恢复到该版本成功' });
  }
  // 分页 页面显示条数切换
  paginate(event) {
    //每页显示的条数
    this.flowCatalogBean.rows = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.flowCatalogBean.page = currentPage;
    //调用查询的方法
    this.queryClick();
    this.flowCatalogBean.page = 1;

  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }




  // clickBtb(){
  //   this.waitSelUserList= this.commfunc.query('245000','');
  //   console.log(this.waitSelUserList.erro)
  //   if(this.waitSelUserList.erro){
  //     this.msgs=[]
  //     this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro.message });
  //   }
  //   this.invalue=this.waitSelUserList.waitSelUserList;
  //   this.aaaa=this.waitSelUserList.display;
  // }
}
