import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { SharedBasisModule } from 'app/sharedBasis.module';
import { FlowCatalogBean } from "../bean/flow-catalog.bean";
import { Message } from 'primeng/primeng';
import { WorkflowHttpService } from '../../../http/workflow.http.service';
@Component({
  selector: 'flow-catalog-add',
  templateUrl: './flow-catalog-add.html',
  styleUrls:['flow-catalog-add.scss'],
  providers: [WorkflowHttpService]
})
export class FlowCatalogAdd implements OnInit {

  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  // @Output() queryData: EventEmitter<any> = new EventEmitter();

  userform: FormGroup; //校验

  //提示信息
  msgs: Message[] = [];

  flowCatalogBean: FlowCatalogBean = new FlowCatalogBean();

  constructor(
    public workflowHttpService: WorkflowHttpService,
    public fb: FormBuilder,
  ) {
  }

  ngOnInit() {
    this.userform = this.fb.group({
      'name': new FormControl('', [Validators.pattern(/^\S{1,120}$/), Validators.required]),
      'parentName':new FormControl('',Validators.required),
      'display':new FormControl('')
    });
  }
  ngOnChanges() {
    this.fuzhi()

  }

  fuzhi() {
    if (this.inValue !== undefined) {
      this.flowCatalogBean.parentName = this.inValue.text;
      this.flowCatalogBean.parentId = this.inValue.id;
    }
  }
  // 保存
  preserveClick() {
     //校验的
     for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    //提交服务
    this.workflowHttpService.IProcessDirectoryServiceAdd(this.flowCatalogBean).subscribe(data => {
      if (data.msg.returnCode.code == "success") {
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.msg.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
    })
  }
  //重置
  reset() {
    this.flowCatalogBean.name='',
    this.flowCatalogBean.display=''
  }
}
