import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { PaginatorModule } from 'primeng/primeng';
import { WillDetectionBean } from '../bean/will-detection.bean'
import { WorkflowHttpService } from '../../../../workflow/http/workflow.http.service';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';

@Component({
  selector: 'app-will-detection-history',
  templateUrl: './will-detection-history.component.html'
})
export class WillDetectionHistoryComponent implements OnInit {
  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  willDetectionBean: WillDetectionBean = new WillDetectionBean()
  msgs: Message[] = [];//提示信息
  tableData: any;//接收后台返回表格数据
  total: number;
  tabMesg: any = tableMessage;
  //c查询返回的url拼接数据
  listData: any;
  constructor(
    public workflowHttpService: WorkflowHttpService,
    public commfunc: Commfunc
  ) { }

  ngOnInit() {
    this.query();
  }
  //查询列表
  query() {
    let ch = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.willDetectionBean.userId = ch['userId'];
    this.willDetectionBean.wfInsId = this.inValue.processId;
    this.workflowHttpService.IWorkFlowServicequeryApprovedListbywfInsId(this.willDetectionBean).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.tableData = data.msg.listArray;
        this.total = data.msg.total;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '流程历史查询成功' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
    })
  }
  //查看流程图
  lookFlow() {
    this.queryUrlService();
    setTimeout(() => {
      this.addEverything()
    }, 1000)
  }
  //拼接url
  addEverything() {
    window.open('http://' + this.tableData[0].singpath + ':8080' + '/wfd/p/monitor?id=' + this.listData.code + '&wfversion=' + this.listData.processVersion + '&name=' + this.listData.name + '&processid=' + this.tableData[0].wfinstanceid)
  }
  //查询url
  queryUrlService() {
    let paramQ = {
      uniqueReqData: this.tableData[0].wfinstanceid
    }
    this.workflowHttpService.IWorkFlowServicegetWorkFlowIdbypiId(paramQ).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.listData = data.msg;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    })
  }
  //按钮权限
  permissionCheck(id){
	  return	this.commfunc.permissionCheck(id)
	}
}
