import { Component, OnInit } from '@angular/core';
import { PaginatorModule } from 'primeng/primeng';
import { WorkflowHttpService } from '../../http/workflow.http.service';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { FlowVariateBean } from './bean/flow-variate.bean'
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
// import { NumType, DicTypeSelect } from '../../constant/codeValue'
@Component({
  selector: 'flow-variate',
  templateUrl: './flow-variate.component.html',
  styleUrls: ['./flow-variate.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class FlowVariateComponent implements OnInit {

  precredit: string = '流程变量管理';
  //字典类型管道
  dicType: any = [];
  //数据类型
  type: any = [];
  //提示信息
  msgs: Message[] = [];

  // 表体内容
  tableData: any;
  flowVariateBean: FlowVariateBean = new FlowVariateBean()

  //总页数
  total: number;
  //显示使用
  total1: string;

  //模态框的标题
  headerTitle: string = "修改";
  showModel: any;

  //模态框是否显示
  display: boolean = false;

  //传入的值
  inValue: string;
  queryParm: any;
  //修改的参数
  updateValue: any = [];
  tabMesg: any = tableMessage;
  code: any;
  constructor(
    public workflowHttpService: WorkflowHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {

    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.dicType = this.code['DicTypeSelect'];
    this.type = this.code['NumType'];
  }

  ngOnInit() {
    this.flowVariateBean.rows = 10;
    this.flowVariateBean.page = 1;
    // this.flowVariateBean.startRow = 0;
    this.queryClick();
    
    //按回车键查询
    let _thiss = this;
    document.onkeydown = function (event) {
      if (event && event.keyCode === 13) {
        _thiss.queryClick();
      }
    }
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  ngOnChanges() {
    this.queryClick();
  }
  // 重置
  reset() {
    this.flowVariateBean.dicCode = "";
    this.flowVariateBean.type = "";
    this.flowVariateBean.name = "";
    this.flowVariateBean.dicType = "";
  }

  //查询
  queryClick() {
    let dicCode;
    let name;
    let type;
    this.queryParm = { dicCode: this.flowVariateBean.dicCode, name: this.flowVariateBean.name, dicType: this.flowVariateBean.dicType };
    this.flowVariateBean.searchSQL = JSON.stringify(this.queryParm);
    this.workflowHttpService.IDictConfServiceList(this.flowVariateBean).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.tableData = data.msg.data;
        this.total = data.msg.total;
        if (this.total == 0) {
          this.total1 = '0'
        }
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.msg.returnCode.code });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    }
    )
  }

  //分页
  paginate(event) {
    // ;
    //每页显示的条数
    this.flowVariateBean.rows = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.flowVariateBean.page = currentPage;
    //调用查询的方法
    this.queryClick();
    this.flowVariateBean.page = 1;
  }

  // 增加
  add() {
    this.headerTitle = '流程字典';
    this.display = true;
    this.showModel = 1;
  }


  //添加后返回的值
  addCall(param) {
    this.display = param;
    let dicCode;
    let name;
    let type;
    this.queryParm = { dicCode: this.flowVariateBean.dicCode, name: this.flowVariateBean.name, type: this.flowVariateBean.type };
    this.flowVariateBean.searchSQL = JSON.stringify(this.queryParm);
    this.workflowHttpService.IDictConfServiceList(this.flowVariateBean).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.tableData = data.msg.data;
        this.total = data.msg.total;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '新增成功' });
      }
    }
    )

  }

  // // 修改
  update(item) {
    this.headerTitle = '流程字典';
    this.display = true;
    this.showModel = 2;
    this.updateValue = item;
  }


  //修改的回调
  updateCall(param) {
    this.display = param;
    let dicCode;
    let name;
    let type;
    this.queryParm = { dicCode: this.flowVariateBean.dicCode, name: this.flowVariateBean.name, type: this.flowVariateBean.type };
    this.flowVariateBean.searchSQL = JSON.stringify(this.queryParm);
    this.workflowHttpService.IDictConfServiceList(this.flowVariateBean).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.tableData = data.msg.data;
        this.total = data.msg.total;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '修改成功' });
      }
    }
    )
  }

  //删除
  delete(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        // let param = { id: item.id };
        // this.flowVariateBean=item;
        this.workflowHttpService.IDictConfServiceGetDelete(item).subscribe(data => {
          if (data.msg.returnCode.code == 'success') {
            this.queryClick();
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.msg.returnCode.code });
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.errorMessage });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
        })
      }
    });
  }

  //   treeClick(param){
  //     alert(JSON.stringify(param.data));
  //   }
  //返回的值
  outValue(param) {
    this.display = param.display;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}
