import { Component, OnInit } from '@angular/core';
import { PaginatorModule } from 'primeng/primeng';
import { WorkflowHttpService } from '../../../workflow/http/workflow.http.service';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { WaitTaskBean } from './bean/wait-task.bean';
import { TreeNode } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
@Component({
  selector: 'wait-task',
  templateUrl: './wait-task.component.html',
  styleUrls: ['./wait-task.component.scss'],
	providers: [Commfunc]
})
export class WaitTaskComponent implements OnInit {
  // 表头
  precredit: string = '待办任务查询';

  //修改的参数
  updateValue: any = [];

  // 表体内容
  tableData: any;

  //总页数
  total: string;

  //提示信息
  msgs: Message[] = [];

  //模态框的标题
  headerTitle: string = '修改';
  showModel: any;

  //模态框是否显示
  display: boolean = false;

  //传入的值
  inValue: string;
  //bean
  waitTaskBean: WaitTaskBean = new WaitTaskBean();
  //传给子的值
  historyValue: any;
  //表中无数据显示
  tabMesg: any = tableMessage;
  //需传参数
  obj = {
    taskId: '',
    operId: ''
  };
  ch: any = CALENDAR_CN;        //时间格式转换
  temData: Date = new Date();   //时间格式转换

  showTree: Boolean = false;
  //选择的树的value
  choiceValue: any;
  //  选择树的内容
  selectedFile: TreeNode;
  //流程树
  fileTree: any;
  left_tree: any;
  createDate: any;//开始时间 
  constructor(
    public workflowHttpService: WorkflowHttpService,
    public commfunc: Commfunc
  ) { }

  ngOnInit() {
    this.waitTaskBean.rows = 10;
    this.waitTaskBean.page = 1;
    this.queryClick();
  }

  //日期
  showData() {
    this.temData = this.createDate;
  }
  //查询
  queryClick() {
    /**
   * 动态获取userId
   */
    if (this.selectedFile) {
      this.waitTaskBean.id = this.choiceValue;
    }
    this.waitTaskBean.createDate = this.commfunc.transDateN(this.createDate);        
    let ch = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.waitTaskBean.userId = ch['userId'];
    this.waitTaskBean.checkInFlag = 'true'
    this.workflowHttpService.IWorkFlowServicegetCheckTaskList(this.waitTaskBean).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.tableData = data.msg.data
        this.total = data.msg.total;
        if(this.total=='0'){
          this.total='0'
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.code });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //捡入
  over() {
    let ch = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.tableData.forEach(item => {
      this.obj.taskId = item.taskId;
      this.obj.operId = ch['userId']
    });
    this.workflowHttpService.IWorkFlowServicecheckInTask(this.obj).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '检入成功' });
        this.queryClick();
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }


  //分页
  paginate(event) {
    //每页显示的条数
    this.waitTaskBean.rows = event.rows*1;
    //当前页
    let currentPage = event.page + 1;
    this.waitTaskBean.page = currentPage;
    //调用查询的方法
    this.queryClick();
    this.waitTaskBean.page = 1;
  }

  //查看历史流程
  history(car) {
    this.headerTitle = '查看流程历史';
    this.display = true;
    this.showModel = '1';
    this.historyValue = car;
  }
  //查看历史流程回调
  historyCall(event) {
    this.display = false;
    this.queryClick();
  }
  //按钮权限
  permissionCheck(id){
	  return	this.commfunc.permissionCheck(id)
	}

}
