import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { SharedBasisModule } from 'app/sharedBasis.module';
import { MaintainBean } from "../bean/maintain.bean";
import { Message } from 'primeng/primeng';
import { WorkflowHttpService } from '../../../../../workflow/http/workflow.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'maintain-add',
  templateUrl: 'maintain-add.html',
  styleUrls: ['maintain-add.scss'],
  providers: [WorkflowHttpService]
})

export class MaintainAdd implements OnInit {

  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();

  userform: FormGroup; //校验

  //提示信息
  msgs: Message[] = [];

  maintainBean: MaintainBean = new MaintainBean();
  list: any;//接收转换的列表
  list1: any;//
  dicCode: string;
  aliasName: string;
  aName;//转换值需要name-》id
  paramMaintain;//传参集合
  selected: any;
  constructor(
    public workflowHttpService: WorkflowHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc
  ) {
  }

  ngOnInit() {
    this.query();
    this.maintainBean.wfCode = this.inValue.code;
    this.maintainBean.storeName = this.inValue.storeName;
    this.userform = this.fb.group({
      'dicCode': new FormControl('', [Validators.required]),
      'aliasName': new FormControl('', [Validators.required])
    });
  }
  //新增流程变量维护的查询
  query() {
    let params = {
      uniqueReqData: "3"
    }
    this.workflowHttpService.IDictConfServicegetDicListByType(params).subscribe(data => {
      if (data.msg.returnCode.code == "success") {
        // this.list = this.commfunc.listToLabelvalue(data.msg.listArray, 'name', 'dicCode');
        this.list1 = this.commfunc.listToLabelvalue(data.msg.listArray, 'dicCode', 'name');
      }
    })
  }
  //onchange事件
  doChose() {
    this.maintainBean.aliasName = this.maintainBean.dicCode['value'];
    // this.maintainBean.aliasName = this.returnValue(event.value, 'label', 'value');
    // this.aName = this.maintainBean.aliasName
  }
  // doChose1(event) {
  //   this.maintainBean.dicCode = this.returnValue(event.value, 'value', 'label');
  // }
  /**
   * 转换值
   * @param param  值
   * @param colum  字段
   */
  // returnValue(param, colum, valueColum) {
  //   let value: string = "";
  //   this.list.forEach(element => {
  //     if (element[colum] === param) {
  //       value = element[valueColum];
  //     }
  //   });
  //   return value;
  // }
  // 新增确定
  preserveClick() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    //判断是否有下拉更改之后赋值
    // if (this.aName) {
    //   this.paramMaintain = {
    //     wfCode: this.inValue.code,
    //     storeName: this.inValue.storeName,
    //     aliasName: this.maintainBean.dicCode,
    //     dicCode: this.aName,
    //     // dicCode:dic.value,
    //     init: this.maintainBean.init,
    //     maxValue: this.maintainBean.maxValue,
    //     minValue: this.maintainBean.minValue,
    //     version: '',
    //     id: ''
    //   }
    // } else {
    //   this.maintainBean.version = '',
    //     this.maintainBean.id = '',
    //     this.paramMaintain = this.maintainBean
    // }
    let param = {
      wfCode: this.inValue.code,
      storeName: this.inValue.storeName,
      aliasName: this.maintainBean.dicCode['value'],
      dicCode: this.maintainBean.dicCode['label'],
      init: this.maintainBean.init,
      maxValue: this.maintainBean.maxValue,
      minValue: this.maintainBean.minValue,
      version: '',
      id: ''
    }
    this.workflowHttpService.IDictConfServiceAddProcessVariable(param).subscribe(data => {
      if (data.msg.returnCode.code == "success") {
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.msg.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
    // this.maintainBean = new MaintainBean();

  }
  //取消
  cancel() {
    this.dicCode = '';
    this.aliasName = '';
    this.maintainBean = new MaintainBean();
  }

}