import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { WorkflowDesignComponent } from './workflow-design/workflow-design.component';
import { WorkflowRuntimeComponent } from './workflow-runtime/workflow-runtime.component';
import { FlowCatalogComponent } from './workflow-design/flow-catalog/flow-catalog.component';
import { FlowDesignComponent } from './workflow-design/flow-design/flow-design.component';
import { FlowVariateComponent } from './workflow-design/flow-variate/flow-variate.component';
import { VariatePreserveComponent } from './workflow-design/variate-preserve/variate-preserve.component';
import { FlowVersionComponent } from './workflow-design/flow-version/flow-version.component';
import { AlreadyWorkComponent } from './workflow-runtime/already-work/already-work.component';
import { FlowDemoComponent } from './workflow-runtime/flow-demo/flow-demo.component';
import { HistoryFlowComponent } from './workflow-runtime/history-flow/history-flow.component';
import { WaitTaskComponent } from './workflow-runtime/wait-task/wait-task.component';
import { WillDetectionComponent } from './workflow-runtime/will-detection/will-detection.component';
import { WorkflowComponent } from './workflow.component';
import { TemporayWorkFlowComponent } from './temporayWorkFlow/temporayWorkFlow.component';

export const routes: Routes = [

    {
        path: '',
        component: WorkflowComponent,
        children: [
            {
                path: 'workflow-design',
                component: WorkflowDesignComponent,
                children: [
                    {
                        path: 'flow-catalog', component: FlowCatalogComponent
                    },
                    {
                        path: 'flow-design', component: FlowDesignComponent
                    },
                    {
                        path: 'flow-variate', component: FlowVariateComponent
                    },
                    {
                        path: 'variate-preserve', component: VariatePreserveComponent
                    },
                    {
                        path: 'flow-version', component: FlowVersionComponent
                    }
                ]
            },
            {
                path: 'workflow-runtime',
                component: WorkflowRuntimeComponent,
                children: [
                    {
                        path: 'already-work', component: AlreadyWorkComponent
                    },
                    {
                        path: 'flow-demo', component: FlowDemoComponent
                    },
                    {
                        path: 'history-flow', component: HistoryFlowComponent
                    },
                    {
                        path: 'wait-task', component: WaitTaskComponent
                    },
                    {
                        path: 'will-detection', component: WillDetectionComponent
                    }
                ]
            },
            { path: 'temporayWorkFlow', component: TemporayWorkFlowComponent },
            { path: 'activiti-work-flow', loadChildren: './activiti-work-flow/activiti-work-flow.module#ActivitiWorkFlowModule' },
            { path: 'approvalParams-query', loadChildren: './approval-params-query/approval-params-query.module#ApprovalParamsQueryModule' }
        ]
    }
];

export const WorkflowRouting: ModuleWithProviders = RouterModule.forChild(routes);
