import { Component, OnInit } from '@angular/core';
import { PaginatorModule } from 'primeng/primeng';
import { WorkflowHttpService } from '../../../workflow/http/workflow.http.service';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { FlowDemoBean } from './bean/flowDemo.bean';
import { TreeNode } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';

@Component({
  selector: 'flow-demo',
  templateUrl: './flow-demo.component.html',
  styleUrls: ['./flow-demo.component.scss']
})
export class FlowDemoComponent implements OnInit {
  tabMesg: any = tableMessage;
  aaa: any;
  // 表头
  precredit: string = '流程实例查询';

  //修改的参数
  overValue: any = [];
  //查看历史的参数
  historyValue: any = [];
  // 表体内容
  tableData: any;
  //总页数
  total: number;
  total1: string;//显示用
  //模态框的标题
  headerTitle: string = '修改';
  showModel: any;
  //模态框是否显示
  display: boolean = false;
  //传入的值
  inValue: string;
  //流程树
  fileTree: any;
  left_tree: any;
  //提示信息
  msgs: Message[] = [];
  //  选择树的内容
  selectedFile: TreeNode;
  //
  addDisplay: boolean = false;  //流程目录
  id: string;//id
  flowDemoBean: FlowDemoBean = new FlowDemoBean();
  treeData: any;
  desc: string;//流程摘要
  startTime: any;//开始时间
  endAfter: any;
  ch: any = CALENDAR_CN;        //时间格式转换
  temData: Date = new Date();   //时间格式转换

  showTree: Boolean = false;
  //选择的树的value
  choiceValue: any;
  // 树展开需要参数
  expandRecursive(node: TreeNode, isExpand: boolean) {
    node.expanded = isExpand;
    if (node.children) {
      node.children.forEach(childNode => {
        this.expandRecursive(childNode, isExpand);
      });
    }
  }
  processDefinitionId: string;//清空用
  constructor(
    public workflowHttpService: WorkflowHttpService,
    public commfunc: Commfunc
  ) { }

  ngOnInit() {
    this.flowDemoBean.rows = 10;
    this.flowDemoBean.page = 1;
    this.queryClick1();
    this.queryClick();
    //按回车键查询
    let _thiss = this;
    document.onkeydown = function (event) {
      if (event && event.keyCode === 13) {
        _thiss.queryClick();
      }
    }
  }

  //流程目录展开
  toTree(data) {
    data.forEach(item => {
      item['label'] = item['text']
      item['value'] = item
      if (item.children) {
        this.toTree(item.children)
      }
    });
    return data
  }
  //选择树的内容
  nodeSelect(event) {
    if (event.node) {
      this.choiceValue = event.node.value.id;
      this.selectedFile = event.node.label;
      this.showTree = false
    }
  }

  nodeUnselect(event) {
    this.msgs = [];
    this.msgs.push({ severity: 'info', summary: 'Node Unselected', detail: event.node.label });
  }
  //查询流程目录树
  queryClick1() {
    this.workflowHttpService.IProcessDirectoryServiceSearchForDesigner({ 'uniqueReqData': '-1' }).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.left_tree = this.toTree(data.msg.listArray);
        this.left_tree.forEach(node => {
          this.expandRecursive(node, true);
        });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //点击查询
  queryClick() {
    this.total1 = '';
    if (this.selectedFile) {
      this.flowDemoBean.processDefinitionId = this.choiceValue;
    }
    this.flowDemoBean.endAfter = this.commfunc.transDateN(this.endAfter);
    let ch = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.flowDemoBean.userId = ch['userId'];
    this.flowDemoBean.state = 'active'
    this.workflowHttpService.IWorkFlowServicegetProcessInsList(this.flowDemoBean).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.tableData = data.msg.data
        this.total = data.msg.total;
        if (this.total == 0) {
          this.total1 = '0'
        }
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.msg.returnCode.code });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //流程编号
  //点击事件控制树的显示隐藏
  clickEvn() {
    this.showTree = !this.showTree;
  }
  //流程编码的回调
  treeCall(param) {
    this.display = false;
    this.flowDemoBean.id = param.id;
  }
  // 结束流程
  over(car) {
    this.headerTitle = '结束流程';
    this.display = true;
    this.showModel = 1;
    this.overValue = car;
  }
  //结束流程回调
  overCall(param) {
    this.display = false;
    this.workflowHttpService.IWorkFlowServicegetProcessInsList(this.flowDemoBean).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.tableData = data.msg.data
        this.total = data.msg.total;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '结束流程成功' });
      }

    })
  }
  // 查看流程历史
  history(car) {
    this.headerTitle = '流程版本信息';
    this.display = true;
    this.showModel = 2;
    this.historyValue = car;
  }
  //查看历史的回调
  historyCall(param) {
    this.display = false;
    // this.queryClick();
  }
  // 重置
  reset() {
    this.selectedFile = null;
    this.endAfter = null;
    this.flowDemoBean.processDefinitionId = '';
    this.flowDemoBean.processInstanceId = '';
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.flowDemoBean.rows = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.flowDemoBean.page = currentPage;
    //调用查询的方法
    this.queryClick();
    this.flowDemoBean.page = 1;
  }
  //日期
  showData() {
    this.temData = this.endAfter;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
