import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { SharedBasisModule } from 'app/sharedBasis.module';
import { FlowCatalogBean } from "../../bean/flow-catalog.bean";
import { Message } from 'primeng/primeng';
import { WorkflowHttpService } from '../../../../http/workflow.http.service';

@Component({
  selector: 'flow-power',
  templateUrl: './flow-power.html',
  providers: [WorkflowHttpService]
})
export class FlowPower implements OnInit {

  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();

  userform: FormGroup; //校验

  //提示信息
  msgs: Message[] = [];

  flowCatalogBean: FlowCatalogBean = new FlowCatalogBean();

  constructor(
    public workflowHttpService: WorkflowHttpService,
    public fb: FormBuilder,
  ) {
  }

  ngOnInit() {

  }

  // 保存
  preserveClick() {

    //提交服务
    this.workflowHttpService.IProcessDefinitionServiceAdd(this.flowCatalogBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: "保存成功" });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: "后台错误" });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  //重置
  reset() {
    
  }
}
