export class FlowVariateBean{
    id:string;//记录Id
    version:number;//记录版本
    dicCode:string;//字典编码
    name:string;//字典名称
    display:string;//字典备注
    type:string;//字典数据类型
    length:string;//字典长度
    dot:number;//字典小数位
    dicType:string;//字典应用类型
    dicValue:number;//字典数据初始值
    modelId:string;//字典模型ID



    data:any;//分页数据数组
    searchSQL:any;// 查询SQL
    countSQL:string;//count查询SQL
    sumSQL:string;//sum查询SQL
    page:number;//当前页数
    rows:number;//每页记录数
    total:number;//总记录数
    startRow:number;//开始记录数

}