import { Component, OnInit } from '@angular/core';
import { PaginatorModule } from 'primeng/primeng';
import { WorkflowHttpService } from '../../../workflow/http/workflow.http.service';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { WillDetectionBean } from './bean/will-detection.bean';
import { TreeNode } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
@Component({
  selector: 'will-detection',
  templateUrl: './will-detection.component.html',
  styleUrls: ['./will-detection.component.scss'],
  providers: [Commfunc,ConfirmationService]
})
export class WillDetectionComponent implements OnInit {

  // 表头
  precredit: string = '待检出任务查询';

  //修改的参数
  updateValue: any = [];

  // 表体内容
  tableData: any;

  //总页数
  total: string;

  //提示信息
  msgs: Message[] = [];

  //模态框的标题
  headerTitle: string = '查看流程历史';

  showModel: any;

  //模态框是否显示
  display: boolean = false;

  //传入的值
  inValue: string;

  //bean
  willDetectionBean: WillDetectionBean = new WillDetectionBean();

  //传给子的值
  historyValue: any;
  //需传参数
  obj={
    taskId:'',
    assignId:'',
    operId:''
  };
  tabMesg: any = tableMessage;
  ch: any = CALENDAR_CN;        //时间格式转换
  temData: Date = new Date();   //时间格式转换

  showTree: Boolean = false;
  //选择的树的value
  choiceValue: any;
  //  选择树的内容
  selectedFile: TreeNode;
  //流程树
  fileTree: any;
  left_tree: any;
  createDate: any;//开始时间 
  constructor(
    public confirmationService: ConfirmationService,
    public workflowHttpService: WorkflowHttpService,
    public commfunc: Commfunc
  ) { }

  ngOnInit() {
    this.willDetectionBean.rows = 10;
    this.willDetectionBean.page = 1;
    this.queryClick();
  }
 
  //日期
  showData() {
    this.temData = this.createDate;
  }
  //查询
  queryClick() {
    if (this.selectedFile) {
      this.willDetectionBean.id = this.choiceValue;
    }
    this.willDetectionBean.createDate = this.commfunc.transDateN(this.createDate); 
    let ch = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.willDetectionBean.userId = ch['userId'];
    this.workflowHttpService.IWorkFlowServicegetCheckTaskList(this.willDetectionBean).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.tableData = data.msg.data
        this.total = data.msg.total;
        if(this.total=='0'){
          this.total='0'
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }


  // 重置
  reset() {
    this.selectedFile = null;
    this.createDate = null;
  }

  //检出
  over(){
    let ch = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.tableData.forEach(item => {        
      this.obj.taskId=item.taskId;
      // this.obj.assignId=ch['userId'];
      this.obj.assignId='sys';
      this.obj.operId=ch['userId']
    });
    this.workflowHttpService.IWorkFlowServicecheckOutTask(this.obj).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '检出成功' });
        this.queryClick();
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //分页
  paginate(event) {
    //每页显示的条数
    this.willDetectionBean.rows = event.rows*1;
    //当前页
    let currentPage = event.page + 1;
    this.willDetectionBean.page = currentPage;
    //调用查询的方法
    this.queryClick();
    this.willDetectionBean.page = 1;
  }

  //查看历史流程
  history(car) {
    this.headerTitle = '查看流程历史';
    this.display = true;
    this.showModel = '1';
    this.historyValue = car;
  }
  //查看历史流程回调
  historyCall(event) {
    this.display = false;
    this.queryClick();
  }
  //按钮权限
  permissionCheck(id){
	  return	this.commfunc.permissionCheck(id)
	}

    //点击事件控制树的显示隐藏
    clickEvn(event) {
      this.showTree = true;
    }
    //流程编码的回调
    treeCall(param) {
      this.display = false;
      this.willDetectionBean.id = param.id;
    }

}
