import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SharedBasisModule } from 'app/sharedBasis.module';
import { FlowCatalogBean } from "../bean/flow-catalog.bean";
import { Message } from 'primeng/primeng';
import { WorkflowHttpService } from '../../../http/workflow.http.service';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';

@Component({
    selector: 'flow-catalog-update',
    templateUrl: './flow-catalog-update.html',
    styleUrls:['flow-catalog-update.scss']
})
export class FlowCatalogUpdate implements OnInit {

    @Input() inValue: any;

    @Output() outValue: EventEmitter<any> = new EventEmitter();
    userform: FormGroup; //校验
    //提示信息
    msgs: Message[] = [];
    //实体
    flowCatalogBean: FlowCatalogBean = new FlowCatalogBean();
    constructor(
        public workflowHttpService: WorkflowHttpService,
        public fb: FormBuilder,
    ) { }

    ngOnInit() {
        console.log(this.inValue)
        this.userform = this.fb.group({
            'name': new FormControl('', [Validators.pattern(/^\S{1,120}$/),Validators.required]),
            'parentName': new FormControl(''),
            'display': new FormControl('')
        });
    }

    ngOnChanges() {
        this.fuzhi()

    }
    fuzhi() {
        if (this.inValue !== undefined) {
            console.log(this.inValue,588)
            this.flowCatalogBean.parentName = this.inValue.parent.text;
            this.flowCatalogBean.parentId = this.inValue.parent.id;
            this.flowCatalogBean.version = this.inValue.version;
            this.flowCatalogBean.name = this.inValue.text;
            this.flowCatalogBean.display=this.inValue.display
        }
    }

    //修改保存
    preserveClick() {
        let param = {
            "id": this.inValue.id,
            "display": this.flowCatalogBean.display,
            "parentName": this.inValue.parent.text,
            "parentId": this.inValue.parent.id,
            "name": this.flowCatalogBean.name,
            "version": this.flowCatalogBean.version
        }
        this.workflowHttpService.IProcessDirectoryServiceUpdate(param).subscribe(data => {
            if (data.msg.returnCode.code == "success") {
                this.outValue.emit(false);
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "保存成功" });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        });
    }
    //取消
    reset() {
        this.flowCatalogBean.name='',
        this.flowCatalogBean.display=''
    }

}
