import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SharedBasisModule } from 'app/sharedBasis.module';
import { MaintainBean } from "../bean/maintain.bean";
import { Message } from 'primeng/primeng';
import { WorkflowHttpService } from '../../../../http/workflow.http.service';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'maintain-update',
    templateUrl: './maintain-update.html',
    styleUrls:['maintain-update.scss']
})
export class MaintainUpdate implements OnInit {

    @Input() inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    userform: FormGroup; //校验    
    //提示信息
    msgs: Message[] = [];
    //实体
    maintainBean: MaintainBean = new MaintainBean();
    list: any;//接收转换的列表
    list1: any;//
    dicCode: string;
    aliasName: string;
    aName: any;//转换值
    paramMaintain;//传参
    name;//显示的名称
    constructor(
        public workflowHttpService: WorkflowHttpService,
        public fb: FormBuilder,
        public commfunc: Commfunc
    ) { }

    ngOnInit() {
        console.log(521521, this.outValue)
        this.fuzhi();
        this.query();
        this.userform = this.fb.group({
            'dicCode': new FormControl('', [Validators.required]),
            'aliasName': new FormControl('', [Validators.required])
        });

    }
    //新增流程变量维护的查询
    query() {
        let params = {
            uniqueReqData: "3"
        }
        this.workflowHttpService.IDictConfServicegetDicListByType(params).subscribe(data => {
            if (data.msg.returnCode.code == "success") {
                this.list = this.commfunc.listToLabelvalue(data.msg.listArray, 'aliasName', 'dicCode');
                this.list1 = this.commfunc.listToLabelvalue(data.msg.listArray, 'dicCode', 'aliasName');
            }
        })
    }
    //onchange事件
    doChose(event) {
        this.maintainBean.aliasName = this.returnValue(event.value, 'label', 'value');
        this.aName = this.maintainBean.aliasName;
    }
    doChose1(event) {
        this.dicCode = this.returnValue(event.value, 'value', 'label');
    }
    /**
     * 转换值
     * @param param  值
     * @param colum  字段
     */
    returnValue(param, colum, valueColum) {
        let value: string = "";
        this.list.forEach(element => {
            if (element[colum] === param) {
                value = element[valueColum];
            }
        });
        return value;
    }

    fuzhi() {
        if (this.inValue) {
            this.maintainBean =Object.assign({},this.inValue) ;
        }
    }
    //提交
    preserveClick() {

        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        //改变下拉框的值的时候，转换值
        if (this.aName) {
            this.maintainBean.aliasName = this.maintainBean.dicCode,
            this.maintainBean.dicCode = this.aName
        }

        this.workflowHttpService.IDictConfServiceupdateProcessVariable(this.maintainBean).subscribe(data => {
            if (data.msg.returnCode.code == "success") {
                this.outValue.emit(false);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.code });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
        });
    }

    //重置
    modifyCloseBtn() {
        this.maintainBean.init ='';
        this.maintainBean.maxValue ='';
        this.maintainBean.minValue ='';
    }
}
