import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { PaginatorModule } from 'primeng/primeng';
import { FlowDemoBean } from '../bean/flowDemo.bean'
import { WorkflowHttpService } from '../../../../workflow/http/workflow.http.service';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
// import { PartyType } from '../../../constant/codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
@Component({
  selector: 'flow-demo-over',
  templateUrl: './flow-demo-over.html',
  providers: [ConfirmationService]
})
export class FlowDemoOver implements OnInit {

  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  flowDemoBean: FlowDemoBean = new FlowDemoBean();
  flowDemoB: any;//接收父级参数
  //提示信息
  msgs: Message[] = [];
  state: any[] = [];//结束类型
  code: any;
  constructor(
    public workflowHttpService: WorkflowHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc

  ) {

    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.state = this.code['PartyType'];
  }

  ngOnInit() {

  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  ngOnChanges() {
    this.fuzhi()
  }
  fuzhi() {
    this.flowDemoB = this.inValue;
  }

  //结束流程
  preserveClick() {
    let ch = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.flowDemoBean.userId = ch['userId'];
    this.flowDemoBean.piId = this.inValue.processInstanceId;
    // this.flowDemoBean.endFlag='0';
    this.workflowHttpService.IWorkFlowServicedeleteProcessInstance(this.flowDemoBean).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '结束流程成功' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
    })
  }

}
