import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { SharedBasisModule } from 'app/sharedBasis.module';
import { WorkflowHttpService } from '../http/workflow.http.service';
import { ConfirmationService } from 'primeng/primeng';

@Component({
  selector: 'temporayWorkFlow',
  templateUrl: './temporayWorkFlow.component.html',
  styleUrls:['temporayWorkFlow.component.scss'],
  providers: [WorkflowHttpService,ConfirmationService]
})
export class TemporayWorkFlowComponent implements OnInit {
  userform: FormGroup; //校验
  time:any;
  msgs:any;
  constructor(
    public workflowHttpService: WorkflowHttpService,
    public confirmationService:ConfirmationService
  ) {
  }

  ngOnInit() {

  }

  save(){
    this.confirmationService.confirm({
        message: '确认要提交吗?',
        accept: () => {
            // 条件
            let param = { days: this.time };
            this.workflowHttpService.checkCloseProcess(param).subscribe(data => {
                if (data.returnCode.code == "success") {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.meaasge });
                 
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
            })
        }
    });
  }

}
