import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { SharedBasisModule } from 'app/sharedBasis.module';
import { FlowVariateBean } from '../bean/flow-variate.bean';
import { Message } from 'primeng/primeng';
import { WorkflowHttpService } from '../../../http/workflow.http.service';
// import { NumType, DicTypeSelect } from '../../../constant/codeValue'
import { Commfunc } from "app/@uisftech/common/service/commonFun";



@Component({
  selector: 'flow-variate-add',
  templateUrl: './flow-variate-add.html',
  styleUrls: ['flow-variate-add.scss'],
  providers: [WorkflowHttpService, Commfunc]
})
export class FlowVariateAdd implements OnInit {

  @Output() outValue: EventEmitter<any> = new EventEmitter();

  userform: FormGroup; //校验
  //数据类型
  typeSelect: any = [];
  //字典类型
  dicTypeSelect: any = [];
  addParam: any;

  //提示信息
  msgs: Message[] = [];

  flowVariateBean: FlowVariateBean = new FlowVariateBean();
  code: any;
  constructor(
    public workflowHttpService: WorkflowHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc
  ) {
    this.codeValues() //调用方法，获取全部码值
    this.typeSelect = this.code['NumType'];
    this.dicTypeSelect = this.code['DicTypeSelect'];
  }

  ngOnInit() {
    this.userform = this.fb.group({
      'dicType': new FormControl('', [Validators.required]),
      'dicCode': new FormControl('', [Validators.pattern(/^[\w]{1,255}$/), Validators.required]),
      'type': new FormControl('', [Validators.required]),
      'name': new FormControl('', [Validators.pattern(/^[\w\u4E00-\u9FA5]{1,255}$/), Validators.required]),
      'length': new FormControl('', [Validators.pattern(/^[0-9]{1,8}$/)]),
      'dot': new FormControl('', [Validators.pattern(/^[0-9]{1,4}$/)]),
      'dicValue': new FormControl(''),
      'display': new FormControl(''),
    });
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  // 保存
  preserveClick() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    this.workflowHttpService.IDictConfServiceAdd(this.flowVariateBean).subscribe(data => {
      if (data.msg.returnCode.code == "success") {
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.msg.returnCode.code });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })

  }
  //重置
  reset() {
    this.flowVariateBean = new FlowVariateBean();
  }

}
