import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SharedBasisModule } from 'app/sharedBasis.module';
import { FlowCatalogBean } from "../bean/flow-catalog.bean";
import { Message } from 'primeng/primeng';
import { WorkflowHttpService } from '../../../http/workflow.http.service';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';

@Component({
  selector: 'txt-update',
  templateUrl: './txt-update.html',
  styleUrls:['txt-update.scss']
})
export class TxtUpdate implements OnInit {

  @Input() inValue: any;
  @Input() inValueUpdate: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  userform: FormGroup; //校验 
  //提示信息
  msgs: Message[] = [];
  //实体
  flowCatalogBean: FlowCatalogBean = new FlowCatalogBean();
  constructor(
    public workflowHttpService: WorkflowHttpService,
    public fb: FormBuilder,
  ) { }

  ngOnInit() {
    this.userform = this.fb.group({
      'name': new FormControl('', [Validators.required]),
    });
  }

  ngOnChanges() {
    this.fuzhi()
  }
  //父级拿到的值
  fuzhi() {
    if (this.inValue !== undefined) {
      this.flowCatalogBean.storeName = this.inValue.storeName;
      this.flowCatalogBean.code = this.inValue.code;
      this.flowCatalogBean.name = this.inValue.name;
      this.flowCatalogBean.display = this.inValue.display;
      this.flowCatalogBean.processClass = this.inValue.processClass;
      this.flowCatalogBean.id = this.inValue.id;
    }
  }
  //修改-保存
  preserveClick() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    this.flowCatalogBean.wftype = this.inValueUpdate.wftype,
      this.flowCatalogBean.apptype = this.inValueUpdate.apptype,
      this.flowCatalogBean.bizType = this.inValueUpdate.bizType,
      this.workflowHttpService.IProcessDefinitionServiceUpdate(this.flowCatalogBean).subscribe(data => {
        if (data.msg.returnCode.code == "success") {
          this.outValue.emit(false);
          this.msgs = [];
          this.msgs.push({ severity: 'success', summary: '提示', detail: data.msg.returnCode.message });
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.message });
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
      });

  }
  //取消
  reset() {
    this.flowCatalogBean.code='';
    this.flowCatalogBean.name='';
    this.flowCatalogBean.display='';
    this.flowCatalogBean.processClass='';
  }

}
