import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { SharedBasisModule } from 'app/sharedBasis.module';
import { Message } from 'primeng/primeng';
import { WorkflowHttpService } from '../../../../http/workflow.http.service';
import { VisitControlBean } from '../bean/visit-control.bean';
// import { DoYouHave } from '../../../../constant/codeValue'
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
  selector: 'app-visit-control-add',
  templateUrl: './visit-control-add.component.html',
  providers: [Commfunc]
})
export class VisitControlAddComponent implements OnInit {
  visitControlBean: VisitControlBean = new VisitControlBean();
  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();

  msgs: Message[] = []; //提示信息
  canCreate: any[] = [];//	流程创建权限
  canDelete: any[] = [];	//流程删除权限
  canQuery: any[] = [];	//流程查询权限
  canManager: any[] = [];	//流程管理权限
  groupType: any;
  code: any;
  constructor(
    public workflowHttpService: WorkflowHttpService,
    public commfunc: Commfunc
  ) {
    this.codeValues() //调用方法，获取全部码值
    this.canCreate = this.code['DoYouHave'];
    this.canDelete = this.code['DoYouHave'];
    this.canManager = this.code['DoYouHave'];
    this.canQuery = this.code['DoYouHave'];
  }

  ngOnInit() {
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //确定按钮
  preserveClick() {
    this.workflowHttpService.IProcessDefinitionRoleServiceAdd(this.visitControlBean).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.msg.returnCode.code });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.msg.returnCode.code });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //取消按钮
  reset() {
    this.visitControlBean = new VisitControlBean();
  }
}
